<template>
    <div class="recommend">
        <Title>推荐歌单</Title>
        <ul>
            <router-link tag="li" v-for="rem in recommendMusicList" :key="rem.id" to="/">
                <div>
                    <img :src="rem.picUrl">
                    <span class="bb"><span class="aa"></span>{{rem.playCount | format}}</span>
                </div>
                <p>{{rem.name}}</p>
            </router-link>
        </ul>
        <Title>最新音乐</Title>
        <NewMusicList :newMusicData="newMusicData"></NewMusicList>
    </div>
</template>
<script>
import Title from "../components/Title";
import NewMusicList from "../components/NewMusicList"
export default {
    name:"Recommend",
    data(){
        return {
            recommendMusicList:[],
            newMusicData:[]
        }
    },
    components:{
        Title,
        NewMusicList
    },
    created(){
        this.$axios.get("/personalized?limit=6").then(data=>{
            this.recommendMusicList = data.data.result;
        })
        this.$axios.get("/personalized/newsong").then(data=>{
            this.newMusicData = data.data.result;
            console.log(this.newMusicData);
        })
    },
    filters:{
        format(v){
            return (v/10000).toFixed(1)+"万"
        }
    }
}
</script>
<style lang="less" scoped>
    .recommend{
        ul{
            display:flex;
            flex-wrap: wrap;
            justify-content: space-between;
            li{
                width: 32%;
                margin-bottom:20px;
                div{
                    position:relative;
                    img{
                        width: 100%;
                        display:block;
                        height:auto;
                    }
                    span.bb{
                        font-size: 12px;
                        position:absolute;
                        top:2px;
                        right:3px;
                        color:rgb(250, 248, 248);
                        text-shadow:1px 1px 5px rgba(0,0,0,.5);
                        display:flex;
                        &::before{
                            content:"";
                            display:inline-block;
                            width: 20px;
                            height: 20px;
                            vertical-align: middle;
                            margin-right:-6px;
                            background:url(data:image/svg+xml;base64,) no-repeat 0px 0px/12px 12px;
                        }
                    }
                    
                }
                p{
                    width: 100%;
                    font-size: 13px;
                    padding:6px 2px 0px 6px;
                    word-break:break-all;
                    display:-webkit-box;
                    -webkit-line-clamp:2;
                    -webkit-box-orient:vertical;
                    overflow:hidden;
                    text-align:left;
                }
            }
        }
    }
</style>